.phead{
  margin-top:3vw;
  background-color: #d1d1d1;
  color: #fff;
  width: 100%;
  letter-spacing: .4rem;
  padding-bottom:.4rem;
  font-weight: 700;
  font-size: 1.3rem;

  
}

.container-box {
  top:50%;
  left:50%;
  transform:translate(-50%, -50%);

  text-align:center;
  z-index: 10;
  position: relative;
}
.overlay-box {
  margin: auto;
}

.shadow{
  text-shadow: 1px 1px 1px #000, 3px 3px 5px #000; 
}


.col.s12 {
  transition: color .2s linear;
  display: flex;
  flex-direction: column;
  line-height: 1;
  margin-top: 4px;
}
.col.s12 > a {
  align-self: flex-start;
  width: 100%;
  font-family: "Google Sans",sans-serif;
  font-size:4vw;  
  font-weight: 700;
  line-height: 40px;
  background: none;
  border: none;
  border-radius: 8px;
  box-shadow: 0 0 20px rgba(0,0,0,0);
  color: #425066;
  display: inline-block;
  height: auto;
  outline: none;
  padding: 18px 18px 22px 22px;
  position: relative;
  text-transform: none;
  transition: box-shadow .2s linear,background .2s linear,color .2s linear;
  z-index: 1;
}
.col.s12 > a::before {
  background: linear-gradient(to right, #f30, #ffa42d);
  border-radius: 8px;
  content: '';
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: -1;
}
.col.s12 > a::after {
  background: #fff;
  border-radius: 5px;
  content: '';
  height: calc(100% - 12px);
  left: 6px;
  opacity: 1;
  position: absolute;
  top: 6px;
  transition: .3s opacity linear;
  width: calc(100% - 12px);
  z-index: -1;
}

